.detail {
  height: 100vh;

  padding: 20px 0px 20px 0px;
  &__container {
    height: 100%;
    width: 100%;
    max-width: 1500px;

    @include media("<=phone") {
      height: 100%;
    }
  }

  &__content {
    display: flex;
    align-items: center;
    margin-top: 50px;

    @include media("<=tablet") {
      display: flex;
      flex-flow: column;
      align-items: center;
      text-align: center;
    }

    @include media("<=phone") {
      display: flex;
      flex-flow: column;
      align-items: center;
      text-align: center;
    }

    &__images {
      width: 250px;
      margin-left: 180px;
      cursor: pointer;
      border: 2px solid #fff;

      @include media("<=phone") {
        margin-left: 0px;
      }
      @include media("<=tablet") {
        margin-left: 0px;
      }
    }

    &__description {
      margin-left: 5em;

      width: 50%;
      min-width: 300px;
      align-self: flex-start;

      margin-top: 5rem;

      @include media("<=phone") {
        margin-top: 2rem;
        width: 90%;
        align-self: center;
        margin-left: 1rem;
        text-align: center;
      }
      @include media("<=tablet") {
        margin-top: 2rem;
        width: 90%;
        align-self: center;
        margin-left: 0rem;

        text-align: center;
      }

      h1 {
        font-size: 3.2rem;
        letter-spacing: 0.3rem;
        margin-bottom: 2rem;
        text-align: left;
        font-weight: bold;

        @include media("<=phone") {
          font-size: 2rem;
          text-align: center;
        }
        @include media("<=tablet") {
          font-size: 2rem;
          text-align: center;
        }
      }
      p {
        font-size: 2rem;
        letter-spacing: 0.2rem;
        text-align: left;
        color: papayawhip;
        line-height: 3rem;
        display: -webkit-box;
        -webkit-line-clamp: 6;
        -webkit-box-orient: vertical;
        overflow: hidden;

        @include media("<=phone") {
          font-size: 1.8rem;
          text-align: center;
          margin-bottom: 1.3rem;
          margin-right: 1px;
          -webkit-line-clamp: 4;
        }

        @include media("<=tablet") {
          font-size: 1.8rem;
          text-align: center;
          margin-bottom: 1.3rem;
          margin-right: 1px;
        }
      }

      &__rating {
        align-self: flex-start;
        margin-top: 5%;

        @include media("<=phone") {
          margin-top: 10%;
        }
        @include media("<=tablet") {
          margin-top: 10%;
        }
      }
    }
  }

  &__btncontainer {
    margin-top: 40px;
    display: flex;

    width: 42%;
    justify-content: space-between;

    @include media("<=tabletlarge") {
      width: 60%;

      align-items: center;
      margin-top: 30px;

      justify-content: space-around;
    }

    @include media("<=tablet") {
      width: 50%;
      margin: 0 auto;
      align-items: center;
      margin-top: 30px;

      justify-content: space-around;
    }

    @include media("<=phone") {
      width: 100%;
      margin: 0 auto;

      margin-top: 30px;

      justify-content: space-evenly;
    }
  }

  &__btn {
    padding: 12px;
    background: orangered;
    color: #fff;
    border-radius: 50px;
    font-size: 1.8rem;

    letter-spacing: 0.3rem;
    outline: none;
    &:focus {
      outline: none;
    }

    @include media("<=phone") {
      font-size: 1.6rem;
      letter-spacing: 0.2rem;
      padding: 12px;
    }

    &--book {
      background-color: palegreen;
    }
  }
}
